import {wxRedirect,getOrderGroupByMemid,getShoppingCartList,getSkuVoListBySkuIdList} from "../../lib/js/http.js"
export default {
  data() {
    return {
      userName: '未登录',
      userImg: require('@/static/images/moren.jpeg'),
      shoppingCarNum: 0,
      coupon: 0,
      UC: '',
      UD: '',
      UG: '',
      UP: ''
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
	this.getPayLNum();//获取待支付等信息
	this.getShopingCarNum();//获取购物侧数据
	

    getApp().globalData.login();

  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {
	  this.getShopingCarNum();//获取购物侧数据
    this.pageLoading = false;
	this.userImg=getApp().globalData.userImg;
    var that = this;

    wx.login({
      success: function (res) {
        if (res.code) {
          wx.request({
            url: getApp().globalData.url1 + getApp().globalData.wxRedirect + res.code,
            header: {
              aid: 100
            },
            success: function (res) {
              if (res.data.data.type == 1) {
                getApp().globalData.openId = res.data.data.memberWechat.openId;
                getApp().globalData.userType = res.data.data.type;
                getApp().globalData.shoppingCarNum = res.data.data.memberWechat.shoppingCarNum;
                getApp().globalData.userImg = res.data.data.memberWechat.avatarUrl;
                getApp().globalData.userName = res.data.data.memberWechat.nickname;
                getApp().globalData.memId = res.data.data.memberWechat.id;
                that.setData({
                  userName: res.data.data.memberWechat.nickname,
                  userImg: res.data.data.memberWechat.avatarUrl,
                  shoppingCarNum: res.data.data.memberWechat.shoppingCarNum
                });
                wx.request({
                  url: getApp().globalData.url3 + getApp().globalData.getOrderGroupByMemid + getApp().globalData.memId,
                  data: '',
                  header: {
                    aid: 108
                  },
                  method: 'GET',
                  dataType: 'json',
                  responseType: 'text',
                  success: function (res) {
                    that.setData({
                      UC: parseInt(res.data.data.orderQuantity.UC),
                      UD: parseInt(res.data.data.orderQuantity.UD),
                      UG: parseInt(res.data.data.orderQuantity.UG),
                      UP: parseInt(res.data.data.orderQuantity.UP)
                    });
                  }
                });
              } else {
                getApp().globalData.openId = res.data.data.openId;
                wx.showModal({
                  title: '提示',
                  content: '小可爱，微信授权一键登录',
                  success: function (res) {
                    if (res.confirm) {
                      wx.navigateTo({
                        url: '/pages/authorization/authorization'
                      });
                    } else if (res.cancel) {}
                  }
                });
              }
            }
          });
        } else {}
      }
    });

  },

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    login() {
      if (!this.pageLoading) {
        this.pageLoading = true;
        wx.login({
          success: function (res) {
            if (res.code) {
              wx.request({
                url: getApp().globalData.url1 + getApp().globalData.wxRedirect + res.code,
                header: {
                  aid: 100
                },
                success: function (res) {
                  if (res.data.data.type == 1) {} else {
                    wx.showModal({
                      title: '提示',
                      content: '小可爱，微信授权一键登录',
                      success: function (res) {
                        if (res.confirm) {
                          wx.navigateTo({
                            url: '/pages/authorization/authorization'
                          });
                        } else if (res.cancel) {}
                      }
                    });
                  }
                }
              });
            } else {}
          }
        });
      }
    },

    shoppingCart() {
      var that = this;

      if (getApp().globalData.memId) {
        if (!that.pageLoading) {
          that.pageLoading = true;
          wx.navigateTo({
            url: '/pages/shoppingCart/shoppingCart'
          });
        }
      }
    },

    orderStatus(e) {
      var that = this;

      if (getApp().globalData.memId) {
        if (!that.pageLoading) {
          that.pageLoading = true;
          wx.navigateTo({
            url: '/pages/allOrders/allOrders?orderStatus=' + e.currentTarget.dataset.orderstatus
          });
        }
      }
    },
    address() {
		var that = this;
		if(this.isLogin==true){
			uni.navigateTo({
			       url: '/pages/addressManagement/addressManagement'
			}); 
		}else if(this.isLogin==false){
			uni.showModal({
				title: '',
				content: '暂未登录，请登录',
				success: function (res) {
					if (res.confirm) {
						uni.navigateTo({
						       url: '/pages/login/login'
						}); 
					} else if (res.cancel) {
					}
				}
			});
		}
		// if(this.$store.getters['login/get_isLogin'])
      // if (getApp().globalData.memId) {
      //   if (!that.pageLoading) {
      //     that.pageLoading = true;
      //     wx.navigateTo({
      //       url: '/pages/addressManagement/addressManagement'
      //     });
      //   }
      // }
    },
	goLogin:function(){
		uni.navigateTo({
		       url: '/pages/login/login'
		}); 
	},
	getShopingCarNum:function(){//获取100code
		var that = this;
		// var params ={//post需要  get 如果为拼接不需要   
		// }
		// var url = '/member/api/memberInternal/wxRedirect/'+this.$store.getters['login/get_userInfo'].token; //this.$store.getters['login/get_userInfo'].id;
		// wxRedirect(params,110,url).then(//地址列表aid 113
		// 	(res) => {
		// 		console.log(res.data)
		// 		if(res.data.code==200){
		// 			// this.address=res.data.data.memberAddrVoList;
		// 			// console.log(this.address.length)
		// 		}else{
		// 			uni.showToast({
		// 				icon:'none',
		// 				title: res.data.message,
		// 				duration: 1000,
		// 			});
		// 		}
		// 	},
		// 	(err) => {
		// 	console.log('get err', err)
		// 	}
		// )
		// ---
		var params ={//post需要  get 如果为拼接不需要   
		}
		var url = '/member/api/memberInternal/getShoppingCartList/'+'1' + '/10/' + this.$store.getters['login/get_userInfo'].id + '/1'; //this.$store.getters['login/get_userInfo'].id;
		getShoppingCartList(params,117,url).then(//地址列表aid 117
			(res) => {
				console.log(res.data)
				if(res.data.code==200){
					// this.address=res.data.data.memberAddrVoList;
					// console.log(this.address.length)
					that.getSCarNum(res.data.data.skuId_count)
				}else{
					uni.showToast({
						icon:'none',
						title: res.data.message,
						duration: 1000,
					});
				}
			},
			(err) => {
			console.log('get err', err)
			}
		)
		
	},
	getSCarNum:function(skuIdCount){//获取118code
		var that = this;
		var params ={//post需要  get 如果为拼接不需要  
			skuId_count: skuIdCount
		}
		getSkuVoListBySkuIdList(params,118).then(//地址列表aid 118
			(res) => {
				if(res.data.code==200){
					console.log(res.data)
					this.shoppingCarNum=res.data.data.goodsSkuVoList.length;
					// this.address=res.data.data.memberAddrVoList;
					// console.log(this.address.length)
				}else{
					uni.showToast({
						icon:'none',
						title: res.data.message,
						duration: 1000,
					});
				}
			},
			(err) => {
			console.log('get err', err)
			}
		)
		
	},
	getPayLNum:function(){//获取108code
		var that = this;
		var params ={//post需要  get 如果为拼接不需要   
		}
		var url = '/orders/api/orderInternal/getOrderGroupByMemid/'+this.$store.getters['login/get_userInfo'].id; //this.$store.getters['login/get_userInfo'].id;
		getOrderGroupByMemid(params,108,url).then(//aid 108
			(res) => {
				if(res.data.code==200){
					this.UC=parseInt(res.data.data.orderQuantity.UC);
					this.UD=parseInt(res.data.data.orderQuantity.UD);
					this.UG=parseInt(res.data.data.orderQuantity.UG);
					this.UP=parseInt(res.data.data.orderQuantity.UP);
				}else{
					uni.showToast({
						icon:'none',
						title: res.data.message,
						duration: 1000,
					});
				}
			},
			(err) => {
			console.log('get err', err)
			}
		)
		// ---
		
	}

  },
  computed: {
  	isLogin() {
  		return this.$store.getters['login/get_isLogin']
  	}
  },
};
